label {
  cursor: pointer;
  border-right: 1px solid #d1d4d9;
  display: block;
  height: 4rem;
  width: 4rem;
  position: relative; }
  label span {
    border-radius: 15%;
    height: 3px;
    width: 32px;
    background-color: #f9f9f9;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    transition: all 0.3s ease-out; }
  label span:before {
    border-radius: 15%;
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    background-color: #f9f9f9;
    transition: all 0.3s ease-out;
    top: 400%; }
  label span:after {
    border-radius: 15%;
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    background-color: #f9f9f9;
    transition: all 0.3s ease-out;
    top: -400%; }
  label:hover span {
    height: 0;
    transition: all 0.3s ease-out; }
    label:hover span:before {
      height: 3px;
      transform: rotate(-45deg);
      transition: all 0.3s ease-out; }
    label:hover span:after {
      height: 3px;
      transform: rotate(45deg);
      transition: all 0.3s ease-out; }

.intro {
  width: 100%;
